.profile-landing-news{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.profile-landing-news > p{
    color: var(--font-light);
    text-align: left;
    font-size: 0.9rem;
}

.profile-landing-news-single{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1em;
    padding: 1em;
    border-radius: var(--radius);
    box-shadow:  10px 10px 11px #ebebeb,
             -10px -10px 11px #ebebeb;
}
.profile-landing-news-single p{
    text-align: left;
    font-size: 0.9rem;
    color: var(--font-clr);
}
.profile-landing-news-single small{
    text-align: left;
    color: var(--font-light);
    font-size: 0.8rem;
}
.profile-landing-news-single button{
    width: max-content;
    display: flex;
    align-items: center;
    gap: 0.5em;
    background: transparent;
    color: var(--secondary-clr);
    border: 1px solid var(--secondary-clr);
    border-radius: 5px;
    padding: 0.5em 1em;
}