.conversation-preferences{
    position: relative;
    flex: 0 0 65%;
    height: 100%;
    box-shadow: var(--material-shadow);
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
}

.conversation-preferences-top{
    width: 100%;
    height: 15%;
    display: flex;
    align-items: center;
    gap: 0.5em;
    padding: 1em;
    background: var(--main-clr);
    border-bottom: 1px solid var(--border-clr);
}
.conversation-preferences-top div:nth-child(1){
    width: 35px;
    height: 35px;
    border-radius: 50%;
    background: var(--secondary-clr);
    color: var(--main-clr);
    display: flex;
    align-items: center;
    justify-content: center;
}
.conversation-preferences-top div:nth-child(2){
    display: flex;
    flex-direction: column;
}
.conversation-preferences-top div:nth-child(2) p{
    /* color: var(--font-clr); */
    text-transform: capitalize;
    text-align: left;
    font-size: 14px;
}
.conversation-preferences-top div:nth-child(2) small{
    text-align: left;
    color: var(--font-light);
    font-size: 12px;
}
.conversation-preferences-top div:nth-child(2) span{
    color: var(--secondary-clr);
    text-transform: lowercase;
}
.conversation-preferences-top div:nth-child(3){
    position: absolute;
    right: 1em;
    display: flex;
    align-items: center;
    gap: 0.5em;
}
.conversation-preferences-top-icon{
    color: var(--success-clr);
    font-size: 24px;
}

/* Top options */
.conversation-preferences-top-options-wrapper{
    position: relative;
}
.conversation-preferences-top-options-btn{
    cursor: pointer;
    background: transparent;
    border: none;
}
.conversation-preferences-top-options{
    position: absolute;
    top: 30px;
    right: 10px;
    background: var(--main-clr);
    box-shadow: var(--material-shadow);
    border-radius: 5px;
    overflow: hidden;
    z-index: 2;
}
.conversation-preferences-top-options button{
    font-size: 12px;
    background: transparent;
    border: none;
    border-bottom: 1px solid var(--border-clr);
    padding: 0.4em 0.8em;
    white-space: nowrap;
    display: flex;
    cursor: pointer;
}
.conversation-preferences-top-options button:hover{
    background: var(--secondary-clr);
    color: var(--main-clr);
}


/* Intro */
.conversation-preferences-intro{
    height: 85%;
    display: flex;
    flex-direction: column;
    gap: 1em;
    padding: 2em;
}
.conversation-preferences-intro h1{
    width: 100%;
    font-size: 15px;
    font-weight: 500;
    margin: 0;
    text-align: left;
}
.conversation-preferences-intro p{
    color: var(--font-clr);
    line-height: 1.5em;
    font-size: 14px;
    margin: 0;
    text-align: left;
}
.conversation-preferences-intro h1 span{
    color: var(--secondary-clr);
    text-transform: capitalize;
}
.conversation-preferences-intro p span{
    text-transform: capitalize;
}
.conversation-preferences-intro-btns{
    margin-top: 1em;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.conversation-preferences-intro-ok-btn{
    width: max-content;
    background: var(--secondary-clr);
    color: var(--main-clr);
    border: none;
    border-radius: 5px;
    padding: 0.5em 1em;
    cursor: pointer;
}
.conversation-preferences-intro-ok-btn:hover{
    background: var(--intense-clr);
}
.conversation-preferences-intro-hide-btn{
    display: flex;
    align-items: center;
    gap: 0.3em;
    font-size: 12px;
    color: var(--font-light);
}


/* Settings */
.conversation-preferences-settings-wrapper{
    height: 85%;
    display: flex;
    flex-direction: column;
    gap: 1.5em;
    overflow: auto;
    padding: 1em;
}
.conversation-preferences-settings-wrapper::-webkit-scrollbar {
    width: 10px;
}
.conversation-preferences-settings-wrapper::-webkit-scrollbar-track {
    background: #f1f1f1;
}
.conversation-preferences-settings-wrapper::-webkit-scrollbar-thumb {
    background: #888;
}
.conversation-preferences-settings-wrapper::-webkit-scrollbar-thumb:hover {
    background: #555;
}
.conversation-preferences-settings-wrapper h1{
    font-size: 14px;
    text-align: left;
    font-weight: normal;
    margin: 0;
}

.conversation-preferences-settings{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1em;
    padding: 1em;
}
.conversation-preferences-settings > p{
    text-align: left;
    color: var(--font-clr);
    font-size: 16px;
    font-weight: 500;
}
.conversation-preferences-settings small{
    text-align: left;
    color: var(--font-light);
    margin-bottom: 1em;
}
.conversation-preferences-settings small span{
    text-transform: capitalize;
}

.conversation-preferences-settings-radiobtns-wrapper{
    display: flex;
    gap: 1em;
}
.conversation-preferences-settings-radiobtns{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.3em;
}
.conversation-preferences-settings-radiobtns p{
    color: var(--font-clr);
    text-align: left;
    font-size: 14px;
    margin: 0;
}
.conversation-preferences-settings-radiobtns > div{
    display: flex;
    flex-flow: row wrap;
    gap: 0.5em;
}
.conversation-preferences-settings-radiobtns > div > div{
    display: flex;
    align-items: flex-start;
    color: var(--font-light);
    font-size: 14px;
}

.conversation-preferences-settings-input-wrapper{
    display: flex;
    gap: 1em;
}
.conversation-preferences-settings-input{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.3em;
}
.conversation-preferences-settings-input > div{
    color: var(--font-clr);
    text-align: left;
    font-size: 14px;
    margin: 0;
}
.conversation-preferences-settings-input input{
    width: 100%;
    outline: none;
    background: transparent;
    border: 1px solid var(--font-light);
    border-radius: 5px;
    padding: 0.5em 1em;
    color: var(--font-light);
}
.conversation-preferences-settings-input input:focus{
    border-bottom: 2px solid var(--secondary-clr);
}


.conversation-preferences-settings-finish{
    width: max-content;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1em;
    font-size: 14px;
    margin-top: 1em;
}
.conversation-preferences-settings-finish button{
    background: var(--secondary-clr);
    border: none;
    padding: 0.5em 1em;
    color: var(--main-clr);
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s;
}
.conversation-preferences-settings-finish button:hover{
    background: var(--intense-clr);
}


/* Connection approval  */
.conversation-preferences-reject{
    width: 100%;
    background: var(--main-clr);
    border-top: 1px solid var(--border-clr);
    border-bottom: 1px solid var(--border-clr);
    display: flex;
    flex-direction: column;
    padding: 1em;
    gap: 1em;
}
.conversation-preferences-reject > div:nth-child(1){
    display: flex;
    flex-direction: column;
    gap: 0.5em;
}
.conversation-preferences-reject > div:nth-child(1) p{
    color: black;
    text-align: left;
}
.conversation-preferences-reject > div:nth-child(1) small{
    color: var(--font-light);
    font-size: 12px;
    text-align: left;
    width: 70%;
}
.conversation-preferences-reject > div:nth-child(2){
    width: max-content;
    display: flex;
    height: max-content;
    gap: 0.5em;
}
.conversation-preferences-reject-btn{
    background: transparent;
    border: 1px solid var(--secondary-clr);
    color: var(--secondary-clr);
    border-radius: 5px;
    padding: 0.5em 1em;
    display: flex;
    align-items: center;
    cursor: pointer;
}
.conversation-preferences-reject-btn:hover{
    background: var(--main-light);
}
.conversation-preferences-approve-btn{
    background: var(--secondary-clr);
    border: none;
    color: var(--main-clr);
    padding: 0.5em 1em;
    border-radius: 5px;
    cursor: pointer;
}
.conversation-preferences-approve-btn:hover{
    background: var(--intense-clr);
}


/* Participant */
.conversation-preferences-participant{
    position: relative;
    width: 95%;
    margin: 1em;
    padding: 1em;
    border: 1px solid var(--border-clr);
    box-shadow: var(--material-shadow-intense);
    border-radius: var(--radius);
    display: flex;
    flex-direction: column;
    gap: 0.5em;
}
.conversation-preferences-participant-close-btn{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 1em;
    background: transparent;
    color: var(--font-clr);
    border: none;
    cursor: pointer;
}
.conversation-preferences-participant-close-btn:hover{
    transform: translateY(-50%) scale(1.2);
}
.conversation-preferences-participant h1{
    font-size: 16px;
    text-align: left;
    font-weight: 500;
    color: var(--font-clr);
    margin: 0;
}
.conversation-preferences-participant-p{
    max-width: 95%;
    font-size: 14px;
    text-align: left;
    color: var(--font-light);
}
.conversation-preferences-participant-p span{
    font-weight: 500;
    color: var(--secondary-clr);
}
.conversation-preferences-participant h1 span{
    text-transform: capitalize;
}
.conversation-preferences-participant-empty{
    width: 100%;
    color: var(--font-light);
    text-align: left;
    font-size: 12px;
}

/* Completed */
.conversation-preferences-completed{
    border-left: 1px solid var(--border-clr);
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 3em;
}
.conversation-preferences-completed img{
    width: 40%;
    height: 40%;
}
.conversation-preferences-completed h1{
    font-size: 16px;
    color: var(--font-clr);
}
.conversation-preferences-completed p{
    color: var(--font-light);
    font-size: 14px;
    width: 70%;
}