.connections-check-wrapper{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 30;
    background: rgba(46, 43, 43, 0.825);
    display: flex;
    align-items: center;
    justify-content: center;
}

.connections-check{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.5em;
    padding: 3em 1em;
    background: var(--main-clr);
    box-shadow: var(--material-shadow);
    border: 1px solid var(--border-clr);
    border-radius: var(--radius);
    overflow: hidden;
}
.connections-check img{
    position: absolute;
    width: 100%;
    transform: translateX(-50%) translateY(-50%);
    opacity: 0.3;
    user-select: none;
    z-index: 0;
    display: none;
}
.connections-check-close-btn{
    position: absolute;
    top: 1em;
    right: 1em;
    background: var(--main-clr);
    border: 1px solid var(--secondary-clr);
    color: var(--secondary-clr);
    border-radius: 5px;
    padding: 0.5em 1em;
    cursor: pointer;
    transition: all 0.3s;
}
.connections-check-close-btn:hover{
    background: var(--secondary-clr);
    color: var(--main-clr);
}
.connections-check h1{
    width: max-content;
    font-size: 1.1rem;
    font-weight: 400;
    color: var(--font-clr);
    margin: 0;
}
.connections-check p{
    width: max-content;
    color: var(--font-light);
    width: 60%;
    line-height: 1.5em;
    margin: 0;
}
.connections-check-name{
    color: var(--secondary-clr);
    font-weight: 500;
}
.connection-check-btns{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5em;
}
.connection-check-btns button{
    padding: 1em 2em;
    border-radius: 5px;
    border: none;
    background: var(--secondary-clr);
    color: var(--main-clr);
    cursor: pointer;
}
.connection-check-btns button:nth-child(2){
    background: var(--third-clr);
    color: var(--secondary-clr);
}