.outlinks{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1em;
    gap: 1em;
}
.outlinks-title{
    font-size: 18px;
    color: var(--font-clr);
    text-align: left;
    margin: 0;
}
.outlinks-sub{
    font-size: 14px;
    text-align: left;
    color: var(--font-clr);
    margin: 0;
}


/* Wrapper for outlinks table */
.outlinks-content{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}
.outlinks-not-found{
    padding: 0.5em;
    border: 1px solid var(--border-clr);
}
.outlinks-not-found p{
    color: var(--secondary-clr);
    text-align: left;
    font-size: 14px;
}


/* Nav */
.outlinks-nav{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 1em 0;
}
.outlinks-nav-search{
    border: 1px solid var(--border-clr);
    border-radius: var(--radius);
    display: flex;
    align-items: center;
    gap: 0.5em;
    padding: 0.5em 1em;
}
.outlinks-nav-search input{
    background: transparent;
    border: none;
    outline: none;
}
.outlinks-nav select{
    padding: 0.7em 1.4em;
    border: 1px solid var(--border-clr);
    border-radius: var(--radius);
    cursor: pointer;
}
.outlinks-nav option{
    cursor: pointer;
}

/* Outlinks titles */
.outlinks-table-title{
    width: 100%;
    display: flex;
    border: 1px solid var(--border-clr);
    border-radius: var(--radius) var(--radius) 0 0;
}
.outlinks-table-title div {
    width: 15%;
    padding: 0.3em 0.6em;
    font-size: 14px;
    color: var(--font-light);
    text-align: left;
}
.outlinks-table-title div:nth-child(1), .outlinks-table-title div:nth-child(2){
    width: 20%;
}
.outlinks-table-title div:nth-child(6), .outlinks-table-title div:nth-child(7){
    width: 10%;
}


/* Single outlink */
.outlinks-single{
    display: flex;
    align-items: center;
    border: 1px solid var(--border-clr);
}
.outlinks-single > div{
    position: relative;
    width: 15%;
    font-size: 14px;
    padding: 0.5em;
}
.outlinks-single > div:nth-child(1), .outlinks-single > div:nth-child(2){
    width: 20%;
}
.outlinks-single > div:nth-child(6), .outlinks-single > div:nth-child(7){
    width: 10%;
}
.outlinks-single > div p{
    font-size: 13px;
    text-align: left;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: context-menu;
}
#outlinks-single-tooltip{
    position: absolute;
    background: var(--secondary-light);
    color: var(--main-clr);
    border-radius: 5px;
    padding: 0.4em;
    top: -20px;
    left: 5px;
    font-size: 11px;
    z-index: 2;
}
#outlinks-single-tooltip::before{
    content: "";
    position: absolute;
    bottom: -6px;
    left: 10px;
    width: 10px;
    height: 10px;
    background: var(--secondary-light);
    clip-path: polygon(0 0, 0% 100%, 100% 0);
}
.outlinks-single-url{
    color: var(--secondary-clr);
}
.outlinks-single-added{
    font-size: 12px;
}


.outlinks-single-actions{
    position: relative;
    display: flex;
    align-items: flex-start;
}
.outlinks-single-actions > button{
    background: transparent;
    border: none;
    box-shadow: var(--material-shadow);
    border-radius: 5px;
    cursor: pointer;
}
.outlinks-single-actions-options{
    width: max-content;
    background: var(--main-clr);
    border: 1px solid var(--border-clr);
    border-radius: 5px;
    box-shadow: var(--material-shadow);
    position: absolute;
    display: flex;
    flex-direction: column;
    bottom: -60px;
    left: -14px;
    z-index: 2;
    overflow: hidden;
}
.outlinks-single-actions-options > button{
    background: transparent;
    border: none;
    padding: 0.5em 1em;
    cursor: pointer;
}
.outlinks-single-actions-options > button:hover{
    background: var(--secondary-clr);
    color: var(--main-clr);
}

/* Skeleton */
.outlinks-skeleton{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1em;
    gap: 1em;
}
.outlinks-skeleton div{
    background: var(--main-light);
    border-radius: var(--radius);
}
.outlinks-skeleton div:nth-child(1){
    width: 20%;
    height: 20px;
}
.outlinks-skeleton div:nth-child(2){
    width: 30%;
    height: 20px;
}
.outlinks-skeleton div:nth-child(3){
    margin-top: 1em;
    width: 100%;
    height: 40px;
}
.outlinks-skeleton div:nth-child(4){
    width: 100%;
    height: 200px;
}